import React, { useState } from "react";
import AddBitComponent from "./AddBitComponent";
import { useDispatch, useSelector } from "react-redux";
import { bitOperations } from "app/store/state/active-bit";

function AddBit(props) {
  const dispatch = useDispatch();
  const tinyMceApiKey = useSelector((state) => state.app.tinyMceApiKey);

  const [bitData, setBitData] = useState({
    name: "",
    slug: "",
    content: "",
    tags: "",
    author: "",
  });

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = bitData[field];
      if (newValue !== oldValue) {
        setBitData({
          ...bitData,
          [field]: newValue,
        });
      }
    }
  };

  const handleEditorChange = (field, overview) => {
    setBitData({
      ...bitData,
      [field]: overview,
    });
  };

  const handleTagsChange = (field, tags) => {
    setBitData({
      ...bitData,
      [field]: tags,
    });
  };

  const reset = () => {
    setBitData({
      name: "",
      slug: "",
      content: "",
      tags: "",
      author: "",
    });
  };

  const saveBitDetails = (e) => {
    e.preventDefault();
    reset();
    dispatch(bitOperations.saveNewBitDetails(bitData));
  };

  return (
    <AddBitComponent
      bitData={bitData}
      tinyMceApiKey={tinyMceApiKey}
      saveBitDetails={saveBitDetails}
      handleChange={handleChange}
      handleEditorChange={handleEditorChange}
      handleTagsChange={handleTagsChange}
    />
  );
}

export default AddBit;
